import { StateCreator } from 'zustand';
import result24Hour from './24hour';
import resultInfo from './info';

export interface WeatherSlice {
  city: string;
  weatherInfo: any;
  weather24Hour: any;
};

export const createWeatherSlice: StateCreator<WeatherSlice> = (set) => ({
  city: '杭州',
  weatherInfo: resultInfo.data,
  weather24Hour: result24Hour.data,
});
