import React, { useRef, useEffect } from 'react';
import { Chart } from '@antv/g2';

interface IProps {
  className?: string;
  data?: any[];
};

const G2Line = (props: IProps) => {
  const chartRef = useRef<HTMLDivElement>(null);
  const chart: any = new Chart({
    container: chartRef.current!,
    autoFit: true,
    margin: 0,
    paddingTop: 40,
    paddingBottom: 20,
  });
  useEffect(() => {
    if (chart && (props?.data || []).length > 0) {
      chart
        .data(props.data)
        .encode('x', 'time')
        .encode('y', 'value')
        .encode('color', 'type')
        .scale('x', { range: [0, 1] })
        .scale('y', { nice: true })
        .axis('x', { tick: false, label: false, title: false })
        .axis('y', { tick: false, label: false, title: false })
        .legend('color', false);

      chart
        .line()
        .label({
          text: 'value',
          style: {
            dy: -20,
            textBaseline: 'top',
            textAlign: 'center',
            fill: '#fff',
          },
          formatter: (d: number) => d + '°'
        })
        .style('stroke', 'white')
        .tooltip(false);

      chart
        .point()
        .encode('shape', 'point')
        .style('fill', 'white')
        .tooltip(false);

      chart.render();
    }
  }, [props?.data]);

  console.log(chart);

  return (
    <div className={props.className} ref={chartRef} />
  );
};

export default G2Line;
