import React from 'react';
import classnames from 'classnames';
import { useWeatherStore } from '@/stores';
import styles from './index.less';
import commonStyles from '../common.less';

const MiniPanel = () => {
  const weatherInfo = useWeatherStore((state) => state.weatherInfo);
  const city = useWeatherStore((state) => state.city);
  const tmp = weatherInfo?.now?.tmp;
  const cond_txt = weatherInfo?.now?.cond_txt;
  const { tmp_max, tmp_min } = weatherInfo?.daily_forecast[0];
  return (
    <div className={styles.container}>
      <div className={classnames(styles.bgCover, commonStyles.yin_d)} />
      <div className={styles.panel}>
        <div className={styles.left}>
          <div className={styles.icon}>{tmp}°</div>
        </div>
        <div className={styles.right}>
          <div className={styles.title}>
            {city}
          </div>
          <div className={styles.info}>
            {`${cond_txt} ${tmp_min}° ~ ${tmp_max}°`}
          </div>
        </div>
      </div>
    </div>
  );
};

export default MiniPanel;
