import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import {
  EnvironmentOutlined,
  FireOutlined,
  CompassOutlined,
} from '@ant-design/icons';
import Copyright from '@/components/Copyright';
import commonStyles from '../common.less';
import styles from './index.less';
import { useWeatherStore } from '@/stores';
import { dateFormat } from '@/utils/tool';
import G2Line from '@/module/components/G2Line';

const DefalutPanel = () => {
  const city = useWeatherStore((state) => state.city);
  const weatherInfo = useWeatherStore((state) => state.weatherInfo);
  const weather24Hour = useWeatherStore((state) => state.weather24Hour);
  const curDayForecast = (weatherInfo.daily_forecast || [])[0] || {};
  console.log('>>> weatherInfo', weatherInfo);
  console.log('>>> weather24Hour', weather24Hour);

  const [lineDateBy24Hour, setLineDateBy24Hour] = useState([]);
  const [lineDateByWeek, setLineDateByWeek] = useState([]);

  useEffect(() => {
    if (weatherInfo?.daily_forecast) {
      setLineDateByWeek((weatherInfo?.daily_forecast || []).flatMap((obj: any) => ([
        { time: obj.date, value: +obj.tmp_min, type: 'tmp_min' },
        { time: obj.date, value: +obj.tmp_max, type: 'tmp_max' },
      ])));
    }
  }, [weatherInfo?.daily_forecast]);

  useEffect(() => {
    if (weather24Hour?.hourly) {
      setLineDateBy24Hour((weather24Hour?.hourly || []).map((i: any) => (
        { time: i.fxTime, value: +i.temp }
      )));
    }
  }, [weather24Hour?.hourly]);

  console.log('>>>> lineDateBy24Hour', weather24Hour);
  console.log('>>>> lineDateByWeek', weather24Hour);

  return (
    <div className={styles.container}>
      <div className={classnames(styles.bgCover, commonStyles.yin_d)} />
      <div className={styles.panel}>
        <div className={classnames(styles.topPanel, styles.flex1)}>
          <div className={styles.nowPanel}>
            <div className={styles.weatherNowtmp}>
              <div className={styles.nowTmp}>{`${weatherInfo.now.tmp}°`}</div>
              <div className={styles.info}>
                <div>
                  <EnvironmentOutlined />
                  <span>{city} 发布于: {dateFormat(weather24Hour.updateTime, 'MM-dd hh:mm')}</span>
                </div>
                <div>
                  <FireOutlined />
                  <span>{`${weatherInfo.air_now_city.qlty} / ${weatherInfo.air_now_city.aqi}`}</span>
                </div>
                <div>
                  <CompassOutlined />
                  <span>{`${weatherInfo.now.cond_txt} ${weatherInfo.now.wind_dir} ${weatherInfo.now.wind_sc}`}</span>
                </div>
              </div>
            </div>
          </div>
          <div className={styles.weatherInfo}>
            <div><h5>温度</h5><span>{`${curDayForecast.tmp_min}°~${curDayForecast.tmp_max}°`}</span></div>
            <div><h5>湿度</h5><span>{`${weatherInfo.now.hum}%`}</span></div>
            <div><h5>降水</h5><span>{`${weatherInfo.now.pcpn}mm`}</span></div>
            <div><h5>气压</h5><span>{`${weatherInfo.now.pres}hPa`}</span></div>
            <div><h5>日出</h5><span>{weatherInfo.sun.rise}</span></div>
            <div><h5>日落</h5><span>{weatherInfo.sun.set}</span></div>
          </div>
        </div>
        <div className={classnames(styles.listPanel, styles.flex2, styles.hoursPanel)}>
          <h3>24小时趋势图</h3>
          <ul>
            {(weather24Hour.hourly || []).map((item: any, index: any) => (
              <li key={`${index}`}>
                <div className={styles.text}>
                  {`${dateFormat(item.fxTime, 'h')}时`}
                </div>
                <div className={styles.text}>{item.icon}</div>
                <div className={styles.flex1} />
              </li>
            ))}
            <div className={styles.linePanel}>
              <G2Line data={lineDateBy24Hour} />
            </div>
          </ul>
        </div>
        <div className={classnames(styles.listPanel, styles.flex4, styles.weekPanel)}>
          <h3>7日趋势图</h3>
          <ul>
            {(weatherInfo.daily_forecast || []).map((item: any, index: any) => (
              <li key={`${index}`}>
                <div className={styles.text}>{dateFormat(item.date, 'MM-dd')}</div>
                <div className={styles.text}>{item.cond_txt_d}</div>
                <div className={styles.flex1} />
                <div className={styles.text}>{`${item.wind_sc}级`}</div>
              </li>
            ))}
            <div className={styles.linePanel}>
              <G2Line data={lineDateByWeek} />
            </div>
          </ul>
        </div>
      </div>
      <div className={styles.copyright}>
        <Copyright />
      </div>
    </div>
  );
};

export default DefalutPanel;
